// MobileNav.js
import { NavLink, Link, useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import Swal from 'sweetalert2';
import buscar from '../images/lupa.png';
import home from '../images/home.png';
import producto from '../images/producto.png';
import play from '../images/play.png';
import compania from '../images/compania.png';
import servicio from '../images/servicios.png';
import clientes from '../images/clientes.png';
import aguila from '../images/aguila.png';
import carrito from '../images/carrito.png';
import perfil from '../images/user.png';

function MobileNav() {
  const [dropdownVisible, setDropdownVisible] = useState(false);
  const [isCartVisible, setIsCartVisible] = useState(false);
  const [isSearchExpanded, setIsSearchExpanded] = useState(false);
  const navigate = useNavigate();
  const isAuthenticated = Boolean(localStorage.getItem('token'));

  const toggleDropdown = (e) => {
    e.stopPropagation();
    setDropdownVisible(!dropdownVisible);
  };

  const toggleCart = (e) => {
    e.stopPropagation();
    setIsCartVisible(!isCartVisible);
  };

  const closeCart = () => {
    setIsCartVisible(false);
  };

  const toggleSearch = (e) => {
    e.stopPropagation();
    setIsSearchExpanded(!isSearchExpanded);
  };

  const handleLogout = async () => {
    Swal.fire({
      title: 'Desea cerrar sesin?',
      showCancelButton: true,
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      confirmButtonColor: '#a11d1c',
      cancelButtonColor: '#202327',
      allowOutsideClick: false,
    }).then(async (result) => {
      if (result.isConfirmed) {
        try {
          const response = await fetch('http://192.168.0.22:8000/api/auth/logout', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            },
          });
  
          if (response.ok) {
            // Limpiar completamente el localStorage
            localStorage.clear();
            navigate('/');
            window.location.reload(); // Recargar la pgina para reflejar el cambio de sesin
          } else {
            Swal.fire('Error', 'Hubo un problema al cerrar sesin', 'error');
          }
        } catch (error) {
          Swal.fire('Error', 'No se pudo conectar con el servidor', 'error');
        }
      }
    });
  };
  

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (!e.target.closest('.cart-menu') && !e.target.closest('#icon-carrito')) {
        setIsCartVisible(false);
      }
      if (!e.target.closest('.dropdown') && !e.target.closest('#icon-perfil')) {
        setDropdownVisible(false);
      }
      if (!e.target.closest('.mobile-search') && !e.target.closest('#icon-mobile-search')) {
        setIsSearchExpanded(false);
      }
    };

    document.addEventListener('click', handleClickOutside);
    return () => {
      document.removeEventListener('click', handleClickOutside);
    };
  }, []);

  return (
    <div>
      {/* Men superior mvil */}
      <nav className="mobile-top-nav">
        <div className="mobile-top-icons">
          <div className={`mobile-search ${isSearchExpanded ? 'expanded' : ''}`}>
            <img src={buscar} alt="lupa" id="icon-mobile-top" onClick={toggleSearch} />
            {isSearchExpanded && <input type="search" placeholder="Buscar en Hosming..." autoFocus />}
          </div>
          {!isSearchExpanded && (
            <>
                <NavLink to="/play" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
                    <img src={play} alt="play" id="icon-mobile-top" />
                </NavLink>
                <NavLink to="/clientes" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
                    <img src={clientes} alt="clientes" id="icon-mobile-top" />
                </NavLink>

              <img src={carrito} alt="carrito" id="icon-mobile-top" onClick={toggleCart} />
              <div className="dropdown" onClick={toggleDropdown}>
                <img src={perfil} alt="perfil" id="icon-mobile-top" />
                {dropdownVisible && (
                  <div className="dropdown-content">
                    {isAuthenticated ? (
                      <>
                        <div id="loguear"><Link to="/profile">Perfil</Link></div>
                        <div style={{textAlign:'center', paddingTop:'10px', paddingBottom:'3px'}} id="registrar" onClick={handleLogout}><span style={{color:'#e10004'}}>Salir</span></div>
                  
                      </>
                    ) : (
                      <>
                        <div id="loguear"><Link to="/login">Iniciar Sesin</Link></div>
                        <div id="registrar"><Link to="/register">Crear Cuenta</Link></div>
                      </>
                    )}
                  </div>
                )}
              </div>
            </>
          )}
        </div>
      </nav>

      {/* Men inferior mvil */}
      <nav className="mobile-bottom-nav">
        <NavLink to="/" className="mobile-nav-link">
          <img src={home} alt="home" />
          <span>Inicio</span>
        </NavLink>
        <NavLink to="/productos" className="mobile-nav-link">
          <img src={producto} alt="productos" />
          <span>Productos</span>
        </NavLink>
        <NavLink to="/servicios" className="mobile-nav-link">
          <img src={servicio} alt="servicio" />
          <span style={{ position: 'relative', top: '3px' }}>Servicios</span>
        </NavLink>
        <NavLink to="/compania" className="mobile-nav-link">
          <img src={compania} alt="compania" />
          <span>Industria</span>
        </NavLink>
        <NavLink to="/chat" className="mobile-nav-link">
          <img src={aguila} alt="chat" />
          <span>Chat</span>
        </NavLink>
      </nav>

      {/* Carrito desplegable */}
      <div className={`cart-overlay ${isCartVisible ? 'cart-overlay-visible' : ''}`}>
        <div className={`cart-menu ${isCartVisible ? 'cart-menu-open' : ''}`} onClick={(e) => e.stopPropagation()}>
          <button className="close-btn" onClick={closeCart}>X</button>
          <h3>Artculo agregado a tu carrito</h3>
          <p>Tu carrito est vaco</p>
          <button>Compra ahora</button>
        </div>
      </div>
    </div>
  );
}

export default MobileNav;
