// src/components/ContactsModal.js
import React from 'react';

const ContactsModal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
        <div className="modal-container" id="modal-edit-user-general">
            <button 
            className="modal-close-button" 
            onClick={onClose}
            >
            
            </button>
            <h2>Editar Datos</h2>
            <hr className="modal-divider" />

            <div className="modal-scroll-content">
            <form className="edit-form-user">
                <label>
                Nombre:
                <input type="text" name="nombre" placeholder="Ingresa tu nombre" />
                </label>

                <label>
                Apellido:
                <input type="text" name="apellido" placeholder="Ingresa tu apellido" />
                </label>

                <label>
                Fecha de Nacimiento (opcional):
                <input type="date" name="fechaNacimiento" />
                </label>

                <label>
                Descripcin (opcional):
                <textarea name="descripcion" placeholder="Escribe una breve descripcin"></textarea>
                </label>

            </form>
            </div>

            <div className="form-buttons">
            <button 
                type="button" 
                className="btn-cancelar-modal" 
                onClick={onClose}
            >
                Cancelar
            </button>
            <button type="submit" className="btn-save">Guardar</button>
            </div>
        </div>
    </div>
  );
};

export default ContactsModal;
