
import React from 'react';
import buscar from '../images/search_negro.png';
import logo_todomaq from '../images/marca-todomaq.jpeg';

const CompanyModal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
        <div className="modal-container" id="mod-container-empresa">
        <button className="modal-close-button" onClick={onClose}></button>
        <h2>Mis empresas</h2>
        <hr className="modal-divider" />

        <div className="modal-content" id="modal-view-empresa">

            <div className="content-search-black">
            <div className="seccion-friends search-black" >
                <img src={buscar} alt='lupa' /> 
                <input type="search" id="search-black-modal" placeholder="Buscar en Hosming..." />
            </div>
            </div>

            <div className='my-empresa-contain'> 
            <div className='img-empresa-info'>
                <div className='the-img-empresa'>
                <img src={logo_todomaq} alt="marca" />
                </div>
                <div className='datos-empresa'>
                <div>
                    <h3>TodoMaq maquinarias</h3>
                </div>
                <div>
                    <span>Santa Cruz, Bolivia</span>
                </div>
                </div>
            </div>

            <div className='view-rol'>
                <div className='the-rol-use'>
                <div className='rol-user-empresa'>
                    <span>SuperAdmin</span>
                </div>
                </div>
            </div>

            </div>


        </div>
        </div>
    </div>
  );
};

export default CompanyModal;
