// src/components/SolicFriendsModal.js
import React, { useState } from 'react';
import buscar from '../images/search_negro.png';
import fotoPerfil from '../images/perfil_prueba.jpeg';
import icon_mensaje from '../images/icon_mensaje.png';

const SolicFriendsModal = ({ isOpen, onClose }) => {
  const [solicFriendsView, setSolicFriendsView] = useState('recibidas'); // 'recibidas' o 'enviadas'

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-container" id="modal-container-solic-friend">
        <button className="modal-close-button" onClick={onClose}></button>
        
        <div className="solic-friend-buttons">
          <button 
            className={`btn-toggleM ${solicFriendsView === 'recibidas' ? 'active-v' : ''}`} 
            onClick={() => setSolicFriendsView('recibidas')}
          >
            Solicitudes Recibidas
          </button>
          <button 
            className={`btn-toggleM ${solicFriendsView === 'enviadas' ? 'active-v' : ''}`} 
            onClick={() => setSolicFriendsView('enviadas')}
          >
            Solicitudes Enviadas
          </button>
        </div>

        <div className="modal-content" id="modal-view-solic-friend">
          {solicFriendsView === 'recibidas' ? (
            <div className="content-recibidas">
              <div className="content-search-black">
                <div className="seccion-friends search-black">
                  <img src={buscar} alt='lupa' /> 
                  <input type="search" id="search-black-modal" placeholder="Buscar en Hosming..." />
                </div>
              </div>
              <div className='my-friends-contain'> 
                <div className='img-friends-info'>
                  <div className='the-img-frind'>
                    <img src={fotoPerfil} alt="fotoPerfil" />
                  </div>
                  <div className='datos-friends'>
                    <h3>Bania Lena Ferrufino</h3>
                    <span>Santa Cruz, Bolivia</span>
                  </div>
                </div>
                <div className="content-new-message-solic">
                  <img src={icon_mensaje} alt="icon_mensaje" />
                </div>
                <div className='mensaje-friends'>
                  <div className='new-mensaje-friend-btn'>
                    <button className='btn-rechazar'><span>Rechazar</span></button>
                    <button className='btn-aceptar'><span>Aceptar</span></button>
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div className="content-enviadas">
              <div className="content-search-black">
                <div className="seccion-friends search-black">
                  <img src={buscar} alt='lupa' /> 
                  <input type="search" id="search-black-modal" placeholder="Buscar en Hosming..." />
                </div>
              </div>
              <div className='my-friends-contain'> 
                <div className='img-friends-info'>
                  <div className='the-img-frind'>
                    <img src={fotoPerfil} alt="fotoPerfil" />
                  </div>
                  <div className='datos-friends'>
                    <h3>Bania Lena Ferrufino</h3>
                    <span>Santa Cruz, Bolivia</span>
                  </div>
                </div>
                <div className='mensaje-friends'>
                  <div className='new-mensaje-friend-btn' id="btn-espera-centrar">
                    <button className='btn-en-espera-solic-enviada'><span>En espera</span></button>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default SolicFriendsModal;
