
import React from 'react';
import buscar from '../images/search_negro.png';
import logo_todomaq from '../images/marca-todomaq.jpeg';

const SolicCompanyModal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
              <div className="modal-container" id="modal-container-solic-empresa">
                <button className="modal-close-button" onClick={onClose}></button>
                <h2>Solicitud a empresas</h2>
                <hr className="modal-divider" />
                <div className="modal-content" id="modal-view-solic-empresa">

                  <div className="content-search-black">
                    <div className="seccion-friends search-black" >
                      <img src={buscar} alt='lupa' /> 
                      <input type="search" id="search-black-modal" placeholder="Buscar en Hosming..." />
                    </div>
                  </div>

                  <div className='my-solic-empresa-contain'> 
                    <div className='img-solic-empresa-info'>
                      <div className='the-img-solic-empresa'>
                        <img src={logo_todomaq} alt="marca" />
                      </div>
                      <div className='datos-solic-empresa'>
                        <div>
                          <h3>TodoMaq maquinarias</h3>
                        </div>
                        <div>
                          <span>Santa Cruz, Bolivia</span>
                        </div>
                      </div>
                    </div>

                    <div className='view-estado-solic-empresa'>
                      <div className='new-mensaje-solic-empresa-btn'>             
                        <div className='btn-en-espera'><span>En espera</span></div>
                      </div>
                    </div>

                  </div>

                </div>
              </div>
            </div>
  );
};

export default SolicCompanyModal;
