

import React from 'react';
import buscar from '../images/search_negro.png';
import logo_todomaq from '../images/marca-todomaq.jpeg';
import logo_aguila from '../images/aguila_circulo.png';
import logo_total from '../images/marca-total.jpeg';

const ProvidersModal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
        <div className="modal-container" id="mod-container-proveedores">
        <button className="modal-close-button" onClick={onClose}></button>
        <h2>Proveedor</h2>
        <hr className="modal-divider" />

        <div className="modal-content" id="modal-view-providers">

            <div className="content-search-black">
            <div className="seccion-friends search-black" >
                <img src={buscar} alt='lupa' /> 
                <input type="search" id="search-black-modal" placeholder="Buscar en Hosming..." />
            </div>
            </div>

            <div className='my-provider-contain'> 
            <div className='img-provider-info'>
                <div className='the-img-provider'>
                <img src={logo_todomaq} alt="marca" />
                </div>
                <div className='datos-provider'>
                <div>
                    <h3>TodoMaq maquinarias</h3>
                </div>
                <div>
                    <span>Santa Cruz, Bolivia</span>
                </div>
                </div>
            </div>

            <div className='mensaje-provider'>
                <div className='new-mensaje-provider-btn'>
                <img src={logo_aguila} alt="me_encanta"/>               
                <button id="btn-mensaje" className='btn-me-encanta'>Mensaje</button>
                </div>
            </div>

            </div>

            <div className='my-provider-contain'> 
            <div className='img-provider-info'>
                <div className='the-img-provider'>
                <img src={logo_total} alt="marca" />
                </div>
                <div className='datos-provider'>
                <div>
                    <h3>Total</h3>
                </div>
                <div>
                    <span>Santa Cruz, Bolivia</span>
                </div>
                </div>
            </div>

            <div className='mensaje-provider'>
                <div className='new-mensaje-provider-btn'>
                <img src={logo_aguila} alt="me_encanta"/>               
                <button id="btn-mensaje" className='btn-me-encanta'>Mensaje</button>
                </div>
            </div>

            </div>


        </div>
        </div>
    </div>
  );
};

export default ProvidersModal;
