// src/components/PostModal.js
import React from 'react';
import icon_imagen from '../images/icono_imagen_modal.png';
import icon_video from '../images/icon_video.png';

const PostModal = ({
  isOpen,
  onClose,
  postContent,
  setPostContent,
  handleFileSelection,
  filePreviews,
  handlePublish,
}) => {
  if (!isOpen) return null;

  return (
    <div className="modal">
      <div className="modal-content-create-publicate">
        <h3 className="title-create-new-publicate">Crear publicacin</h3>
        <button className="close-btn-bio-new" onClick={onClose}>X</button>
        
        <textarea 
          placeholder="Escribe en tu biografa"
          value={postContent}
          onChange={(e) => setPostContent(e.target.value)}
          className="post-textarea"
        />
        
        <button onClick={() => document.getElementById('fileInput').click()} className="upload-button">
          <span id="title-add-multimedia">Agrega multimedia</span>
          <img id="img-publicate-new" src={icon_imagen} alt="icon_imagen" />
          <img id="img-publicate-new" src={icon_video} alt="icon_video" />
        </button>

        {/* Hidden file input */}
        <input 
          id="fileInput"
          type="file"
          accept="image/*,video/*"
          multiple
          onChange={handleFileSelection}
          style={{ display: 'none' }}
        />

        <div className="preview-container">
          {filePreviews.map((preview, index) => (
            <div key={index} className="preview-item">
              {preview.type.startsWith('image/') ? (
                <img src={preview.url} alt={`preview-${index}`} />
              ) : preview.type.startsWith('video/') ? (
                <video src={preview.url} controls width="100" />
              ) : null}
            </div>
          ))}
        </div>

        <button onClick={handlePublish} className="publish-btn">
          <span id="txt-publicate">Publicar</span>
        </button>
      </div>
    </div>
  );
};

export default PostModal;
