// src/components/EditImageModal.js
import React from 'react';
import '../css/meprofile.css'; 
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faImage, faFloppyDisk, faTrash } from '@fortawesome/free-solid-svg-icons';

const EditImageModal = ({ isOpen, onClose, tempImage, selectedImage, handleFileChange, uploadImage, isLoading }) => {
  if (!isOpen) return null;

  return (
    <div className="modal">
      <div className="modal-content" id="modal-edit-img">
        <h3>Foto del perfilll</h3>
        <button className="close-btn" onClick={onClose}>X</button>
        <img className="modal-photo" src={tempImage || 'http://192.168.0.22:8000/' + selectedImage} alt="perfil-modal" />

        <hr className='line-separator'></hr>

        <div className="modal-actions">
          <label htmlFor="fileInput" className="action-btn">
            <FontAwesomeIcon icon={faImage} style={{ fontSize: '18px' }} />
            <span>Nuevo</span>
          </label>
          <input
            type="file"
            id="fileInput"
            style={{ display: 'none' }}
            accept="image/jpeg, image/png, image/jpg"
            onChange={handleFileChange}
            disabled={isLoading}
          />
          {tempImage && (
            <button className="save-btn action-btn" onClick={uploadImage}>
              <FontAwesomeIcon icon={faFloppyDisk} style={{ fontSize: '18px' }} />
              <span>Guardar</span>
            </button>
          )}
          <button className="action-btn delete-btn" disabled={isLoading}>
            <FontAwesomeIcon icon={faTrash} style={{ fontSize: '18px' }} />
            <span>Eliminar</span>
          </button>
        </div>
      </div>
    </div>
  );
};

export default EditImageModal;
