import React from 'react';
import '../css/home.css'; 
import perfilImg from '../images/user_plomo.png';
import producto_plomo from '../images/producto_plomo.png';
import servicio_plomo from '../images/servicio_plomo.png';
import compania_plomo from '../images/compania_plomo.png';
import empresas_plomo from '../images/clientes_plomo.png';
import video_plomo from '../images/play_plomo.png'
import marca_total from '../images/marca-total.jpeg';
import marca_todomaq from '../images/marca-todomaq.jpeg';
import marca_servimoto from '../images/marca-servimotor.jpeg';
import marca_chaqueno from '../images/marca-chaqueno.jpeg';

export const Home = () => {
  // Obtener el nombre del usuario almacenado en el localStorage
  const userName = localStorage.getItem('name');
  let nameUso;
 
  if (userName){
    if(userName.length > 25){
      nameUso = userName.substring(0, 25) + "...";
    }else{
      nameUso = userName;
    }
  }


  return (
    <div className='contenedor-home'>

      <div className='content-left'>

          <div className='accion-icons'>
            {/* Mostrar el nombre del usuario si existe, de lo contrario mostrar "Sin inicio de sesin" */}
            {userName ? (
              <a href='/meprofile' className='user-session'>
                <img src={perfilImg} alt='perfil-izquierdo' />
                <span id='user-start'>{nameUso}</span>
              </a>
            ) : (
              <a href='/login' className='user-session'>
                <img src={perfilImg} alt='perfil-izquierdo' />
                <span id='user-start'>Sin inicio de sesin</span>
              </a>
            )}
                
            <a className='user-session' href='/productos'>
              <img src={producto_plomo} alt='productos' />
              <span>Producto</span>
            </a>
            <a className='user-session' href='/servicios'>
              <img src={servicio_plomo} alt='servicios' />
              <span>Servicios</span>
            </a>
            <a className='user-session' href='/compania'>
              <img src={compania_plomo} alt='compania' />
              <span>Industria</span>
            </a>
            <a className='user-session' href='/video'>
              <img src={video_plomo} alt='empresas' />
              <span>Videos</span>
            </a>
            <a className='user-session' href='/empresas'>
              <img src={empresas_plomo} alt='empresas' />
              <span>Empresas</span>
            </a>

          </div>

          <div className='line-separator' style={{marginTop:'20px', marginBottom:'30px', width:'98.5%'}}>
              <div className='linea-ploma'></div>
          </div>
      
          <div className='marcas-registradas'>
            <div className='marca-empresa'>
              <img src={marca_total} alt='marca_total' />
            </div>

            <div className='marca-empresa'>
              <img src={marca_todomaq} alt='marca_todomaq' />
            </div>

            <div className='marca-empresa'>
              <img src={marca_chaqueno} alt='marca_chaqueno' />
            </div>
      
            <div className='marca-empresa'>
              <img src={marca_servimoto} alt='marca_servimoto' />
            </div>

          </div>
      
      </div>

      <div className='content-right'>
      </div>

    </div>
  );
};
