// src/components/ContactsModal.js
import React from 'react';
import icon_facebook from '../images/facebook.png';
import icon_instagram from '../images/instagram.png';
import icon_tik_tok from '../images/tik-tok.png';
import waza_big from '../images/waza-big.png';
import icon_correo from '../images/correo_circulo.png';
import icon_telefono from '../images/telefono_circulo.png';

const ContactsModal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-container" id="modal-mis-redes">
        <button className="modal-close-button" onClick={onClose}></button>
        <h2>Mis Redes</h2>
        <hr className="modal-divider" />

        <div className="modal-content">
            <div className='my-social-url-content'>
                <div className='the-social-red'>
                <img src={icon_facebook} alt="facebook" />
                <input type="text" placeholder='Pega la url' className='my-url-fb' />
                </div>
                <div className='the-social-red'>
                <img src={icon_instagram} alt="instagram" />
                <input type="text" placeholder='Pega la url' className='my-url-ig' />
                </div>
                <div className='the-social-red'> 
                <img src={icon_tik_tok} alt="tik tok" />
                <input type="text" placeholder='Pega la url' className='my-url-tk' />
                </div>
                <div className='the-social-red'>
                <img src={waza_big} alt="whatsapp" />
                <input type="text" placeholder='Pega la url' className='my-url-w' />
                </div>
                <div className='the-social-red'>
                <img src={icon_telefono} alt="telefono" />
                <input type="text" placeholder='Escribte tu num. de celular' className='my-url-tl' />
                </div>

                <div className='the-social-red'>
                <img src={icon_correo} alt="correo" />
                <input type="text" placeholder='Escribe tu correo' className='my-url-cr' />
                </div>

                     

            </div>
        </div>

        <div className="modal-footer">
          <button className="btn-cancelar-modal" onClick={onClose}>Cancelar</button>
          <button className="btn-save">Guardar</button>
        </div>
      </div>
    </div>
  );
};

export default ContactsModal;
