import './css/navbar.css';
import { BrowserRouter as Router, Route, Routes, NavLink, Link, useLocation, useNavigate } from 'react-router-dom'; 
import { Login } from './pages/login'; 
import { Register } from './pages/register'; 
import { Home } from './pages/home'; 
import { Error404 } from './pages/error404';
import { Meprofile } from './pages/meprofile';
import logo from './images/logo-hosming.png';
import buscar from './images/search.png';
import home from './images/home.png';
import producto from './images/producto.png';
import play from './images/play.png';
import compania from './images/compania.png';
import servicio from './images/servicios.png';
import clientes from './images/clientes.png';
import aguila from './images/aguila.png';
import carrito from './images/carrito.png';
import perfil from './images/user.png';
import menu_tablet from './images/menu.png';
import { useState, useEffect } from 'react';
import MobileNav from './components/mobileNav';
import Swal from 'sweetalert2'; 

function App() {
  return (
    <Router>
      <BodyColorChanger />
      <ConditionalNav />
      <Routes>
        <Route path="/" element={<Home />} /> 
        <Route path="/login" element={<Login />} /> 
        <Route path="/register" element={<Register />} />
        <Route path="/error404" element={<Error404 />} />
        <Route path='/meprofile' element={<Meprofile />} />
      </Routes>
    </Router>
  );
}

// Componente para cambiar el color del body dependiendo de la ruta
function BodyColorChanger() {
  const location = useLocation();
  useEffect(() => {
    document.body.style.backgroundColor = location.pathname === '/login' || location.pathname === '/register' ? '#212529' : '#f5f6f7';
    return () => { document.body.style.backgroundColor = ''; };
  }, [location]);

  return null;
}

// Componente de navegacin condicional
function ConditionalNav() {
  const location = useLocation();
  const navigate = useNavigate();
  const [dropdownVisible, setDropdownVisible] = useState(false);
  const [isCartVisible, setIsCartVisible] = useState(false);
  const [menuTabletVisible, setMenuTabletVisible] = useState(false);
  const [profileImage, setProfileImage] = useState(null);

  const isAuthenticated = Boolean(localStorage.getItem('token'));

  const toggleDropdown = (e) => {
    e.stopPropagation();
    setDropdownVisible(!dropdownVisible);
  };

  const toggleCart = (e) => {
    e.stopPropagation();
    setIsCartVisible(!isCartVisible);
  };

  const toggleMenu = () => {
    setMenuTabletVisible(!menuTabletVisible);
  };

  const closeCart = () => { setIsCartVisible(false); };

  const handleLogout = async () => {
    Swal.fire({
      title: 'Desea cerrar sesin?',
      showCancelButton: true,
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      confirmButtonColor: '#a11d1c',
      cancelButtonColor: '#202327',
      allowOutsideClick: false,
    }).then(async (result) => {
      if (result.isConfirmed) {
        try {
          const response = await fetch('http://192.168.0.22:8000/api/auth/logout', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            },
          });
  
          if (response.ok) {
            // Limpiar completamente el localStorage
            localStorage.clear();
            navigate('/');
            window.location.reload(); // Recargar la pgina para reflejar el cambio de sesin
          } else {
            Swal.fire('Error', 'Hubo un problema al cerrar sesin', 'error');
          }
        } catch (error) {
          Swal.fire('Error', 'No se pudo conectar con el servidor', 'error');
        }
      }
    });
  };
  

  useEffect(() => {
    if (isAuthenticated) {
      const storedProfileImage = localStorage.getItem('profileImage');
      setProfileImage(storedProfileImage);
    } else {
      setProfileImage(null);
    }
  }, [isAuthenticated]);

  // Escuchar el evento `profileImageUpdated` para actualizar la imagen de perfil
  useEffect(() => {
    const updateProfileImage = () => {
      setProfileImage(localStorage.getItem('profileImage') || perfil);
    };

    window.addEventListener("profileImageUpdated", updateProfileImage);

    // Limpia el listener cuando el componente se desmonte
    return () => {
      window.removeEventListener("profileImageUpdated", updateProfileImage);
    };
  }, []);
  

  useEffect(() => {
    console.log('URL de la imagen de perfil:', profileImage);
  }, [profileImage]);

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (!e.target.closest('.cart-menu') && !e.target.closest('#icon-carrito')) setIsCartVisible(false);
      if (!e.target.closest('.dropdown') && !e.target.closest('#icon-perfil')) setDropdownVisible(false);
      if (!e.target.closest('#menu-tablet') && !e.target.closest('.menu-tablet-content')) setMenuTabletVisible(false);
    };
    document.addEventListener('click', handleClickOutside);
    return () => document.removeEventListener('click', handleClickOutside);
  }, []);

  if (['/login', '/register', '/error404'].includes(location.pathname)) return null;

  return (
    <>
      <nav className="container-nav" id="nav-desktop">
        <div className='seccion-nav logo'>
          <Link to="/"> <img id="logo-nav" src={logo} alt="logo" /></Link>
        </div>

        <div className='seccion-nav icons' id="menu-desktop">
          <NavLink to="/" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
            <img src={home} id="icon-home" alt='home' />
          </NavLink>
          <NavLink to="/productos" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
            <img src={producto} id="icon-producto" alt='producto' />
          </NavLink>
          <NavLink to="/servicios" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
            <img src={servicio} id="icon-servicio" alt='servicio' />
          </NavLink>
          <NavLink to="/compania" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
            <img src={compania} id="icon-compania" alt='compania' />
          </NavLink>
          <NavLink to="/play" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
            <img src={play} id="icon-play" alt='play' />
          </NavLink>
          <NavLink to="/clientes" className={({ isActive }) => (isActive ? 'active icon-container' : 'icon-container')}>
            <img src={clientes} id="icon-clientes" alt='clientes' />
          </NavLink>
        </div>

        {/* Codigo solo para pantallas pequeas palet o tablets */}
        <div className='seccion-nav icons' id="menu-tablet" onClick={toggleMenu}>
          <img src={menu_tablet} alt='menu' style={{ width: '45px' }} />
          {menuTabletVisible && (
            <div className="menu-tablet-content">
              <div>
                <NavLink to="/" className="icon-container">
                <img src={home} id="icon-home" alt="home" />
                <span>Inicio</span>
              </NavLink>
              </div>
              <div>
                <NavLink to="/productos" className="icon-container">
                  <img src={producto} id="icon-producto" alt="producto" />
                  <span>Productos</span>
                </NavLink>
              </div>
              <div>
                <NavLink to="/servicios" className="icon-container">
                  <img src={servicio} id="icon-servicio" alt="servicio" />
                  <span>Servicios</span>
                </NavLink>
              </div>
              <div>
                <NavLink to="/compania" className="icon-container">
                  <img src={compania} id="icon-compania" alt="compania" />
                  <span>Industria</span>
                </NavLink>
              </div>
              <div>
                <NavLink to="/play" className="icon-container">
                  <img src={play} id="icon-play" alt="play" />
                  <span>Videos</span>
                </NavLink>
              </div>
              <div>
                <NavLink to="/clientes" className="icon-container">
                  <img src={clientes} id="icon-clientes" alt="clientes" />
                  <span>Empresas</span>
                </NavLink>
              </div>
   
            </div>
          )}
        </div>
        {/* xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx */}


        <div className="seccion-nav search">
          <img src={buscar} alt='lupa' /> 
          <input type="search" id="buscador" placeholder="Buscar en Hosming..." />
        </div>

        <div className='seccion-nav final'>
          <div id="final-v">
            <img src={aguila} id="icon-aguila" alt='chat' />
          </div>

          <div className='super-carrito' id="final-v">
            <img src={carrito} id="icon-carrito" alt='carrito' onClick={toggleCart} />
          </div>

          {/* Dropdown de perfil o autenticacin */}
          <div className="dropdown" id="final-v" onClick={toggleDropdown}>
            <img src={"http://192.168.0.22:8000/"+profileImage || perfil} id="icon-perfil" alt="perfil" /> {/* Modificacin aqu */}
            {dropdownVisible && (
              <div className="dropdown-content">
                {isAuthenticated ? (
                  <>
                    <div id="loguear"><Link to="/profile">Perfil</Link></div>
                    <div style={{textAlign:'center', paddingTop:'10px', paddingBottom:'3px'}} id="registrar" onClick={handleLogout}><span style={{color:'#e10004'}}>Salir</span></div>
                  </>
                ) : (
                  <>
                    <div id="loguear"><Link to="/login">Iniciar Sesin</Link></div>
                    <div id="registrar"><Link to="/register">Crear Cuenta</Link></div>
                  </>
                )}
              </div>
            )}
          </div>

          <div className={`cart-overlay ${isCartVisible ? 'cart-overlay-visible' : ''}`}>
            <div className={`cart-menu ${isCartVisible ? 'cart-menu-open' : ''}`} onClick={(e) => e.stopPropagation()}>
              <button style={{background:'none', color:'#000', fontSize:'17px'}} className="close-btn" onClick={closeCart}>X</button>
              <h3>Artculo agregado a tu carrito</h3>
              <p>Tu carrito est vaco</p>
              <button>Compra ahora</button>
            </div>
          </div>
        </div>
      </nav>
      <MobileNav />
    </>
  );
} 

export default App;
